// styling
import styles from './style.module.scss';

// constants
import {PROFILE_SOCIAL_LINKS} from '@constants/links';

const Socials = ({value ,className}) => {
    const socialAssets = [
        {
            icon: 'discord',
            url: value.discordUrl,
            name: 'Discord'
        },
        {
            icon: 'twitter',
            url: 'https://twitter.com/'+value.twitterUsername,
            name: 'twitter'
        },
        {
            icon: 'telegram',
            url: value.externalUrl,
            name: 'telegram'
        }
    ]
    return (
        <div className={`${className || ''} d-flex g-20`}>
            {
                socialAssets.map(link => {
                    if (link.url != null && link.url !="https://twitter.com/null"){
                    return (
                        <a className={styles.link}
                           key={link.url}
                           href={link.url}
                           target="_blank"
                           rel="noreferrer noopener"
                           aria-label={link.name}>
                            <i className={`icon icon-${link.icon} ${link.icon === 'facebook' ? styles.facebook : ''}`}/>
                        </a>
                    )
                    }
                })
            }
        </div>
    );
}

export default Socials